<template>
    <div>
        <div>
            <b-modal id="modal-sm" size="sm" no-close-on-backdrop title="Confirm">
                <p class="my-2">
                    Are you sure you want to close ?
                </p>
                <template v-slot:modal-footer="{ cancel }">
                    <b-button size="sm" class="float-right mb-3 p-2 pl-5 pr-5" variant="outline-primary" @click="cancel()">
                        Cancel
                    </b-button>
                    <b-button size="sm" class="float-right mb-3 p-2 pl-5 pr-5" variant="primary">
                        Confirm
                    </b-button>
                </template>
            </b-modal>
        </div>
        <div class="d-block header-section">
            <h1>
                <b>Print Order List</b>
            </h1>
            <b-col class="mb-2 col-md-6 float-right search-section">
                <b-form-group
                label
                label-cols-sm="3"
                label-align-sm="right"
                label-size="sm"
                label-for="filterInput"
                class="mb-0"
                >
                <b-input-group>
                    <b-form-input
                    v-model="filter"
                    class="form-control"
                    type="search"
                    id="filterInput"
                    placeholder="Type to Search"
                    ></b-form-input>
                    <b-input-group-append>
                    <b-button :disabled="!filter" @click="filter = ''">
                        <img src="@/assets/images/icons/cross.png" alt />
                    </b-button>
                    </b-input-group-append>
                </b-input-group>
                </b-form-group>
            </b-col>
        </div>
        <div class="container-fluid order-list">
            <!-- START: Card Data-->
            <div class="row">
                <div class="col-12 col-md-12 mt-3 mb-5">
                <div class="card pb-3">
                    <div class="p-3 list-filter">
                    <div class="btn-group">
                        <button
                        type="button"
                        class="btn btn-outline-dark rounded-btn mr-2 mob-mar" :class="{'active' : this.Alltab}" @click="onStatusFilterClicked('All')"
                        >{{this.AllText}}</button>
                    </div>
                    <div class="btn-group">
                        <input type="checkbox" id="submitted" name="orderlist" hidden>
                        <label for="submitted" type="button" class="btn btn-outline-info rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('1')">{{this.SubmittedText}}</label>                
                    </div>
                    <div class="btn-group">
                        <input type="checkbox" id="processing" name="orderlist" hidden>
                        <label for="processing" type="button" class="btn btn-outline-success rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('2')">{{this.ProcessingText}}</label>                
                    </div>

                    <div class="btn-group">
                        <input type="checkbox" id="transit" name="orderlist" hidden>
                        <label for="transit" type="button" class="btn btn-outline-warning rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('4')">{{this.InTransitText}}</label>                
                    </div>

                    <div class="btn-group">
                        <input type="checkbox" id="rejected" name="orderlist" hidden>
                        <label for="rejected" type="button" class="btn btn-outline-danger rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('3')">{{this.RejectedText}}</label>                
                    </div>

                    <div class="btn-group">
                        <input type="checkbox" id="closed" name="orderlist" hidden>
                        <label for="closed" type="button" class="btn btn-outline-danger rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('5')">{{this.ClosedText}}</label>                
                    </div>

                    <div class="btn-group" v-if="$store.state.clientAppDecodedToken.role == 'TsspAdmin'">
                        <button
                        type="button"
                        class="btn btn-info rounded-btn mr-2 mob-mar"
                        @click="createOrder"
                        >
                        Create Order
                        </button>
                    </div>
                    </div>
                    <div class="overflow-auto">
                    <b-table
                        id="orderList"
                        :items="this.getPrintOrders"
                        :filter="filter"
                        filter-debounce="1000"
                        :fields="getfields"
                        :current-page="currentPage"
                        :per-page="perPage"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        sort-icon-left
                        class="table order"
                    >
                        <template v-slot:cell(poNum)="data">
                            <span>{{ data.item.poNum }}</span>
                        </template>
                        <template v-slot:cell(actionPP)="data">
                            <router-link
                                :to="{ name: 'PrintOrderView', params: {printOrderId: data.item.printOrderId, printOrderStatus: data.item.status} }"
                            >{{ data.item.status == 'Submitted' ? "Review" : "View"}}</router-link>
                        </template>
                        <template v-slot:cell(actionAdmin)="data">
                            <router-link
                                class="mr-3"
                                v-if="data.item.status == 'Submitted'"
                                :to="{ name: 'PrintOrderPlacementEdit', params: {printOrderId: data.item.printOrderId} }"
                            >Edit</router-link>
                            <router-link
                                class="mr-3"
                                v-if="data.item.status == 'Submitted' || data.item.status == 'Processing' || data.item.status == 'InTransit' || data.item.status == 'Rejected' || data.item.status == 'Closed'"
                                :to="{ name: 'PrintOrderView', params: {printOrderId: data.item.printOrderId, printOrderStatus: data.item.status} }"
                            >View</router-link>
                            <router-link
                                v-if="data.item.status == 'InTransit'"
                                :to="{ name: 'PrintOrderView', params: {printOrderId: data.item.printOrderId, printOrderStatus: data.item.status} }"
                            >Close</router-link>
                        </template>
                    </b-table>

                    <b-pagination
                        v-model="currentPage"
                        :total-rows="this.totalRows"
                        :per-page="perPage"
                        class="float-right mr-4"
                        first-text="First"
                        prev-text="Prev"
                        next-text="Next"
                        last-text="Last"
                    ></b-pagination>
                    </div>
                </div>
                <!-- START: Card Data-->
                </div>
            </div>
        <!-- END: Card DATA-->
        </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      /*Search */
      filter: "",
      /*Filters*/
      mfIds: [],
      orderStatus: [],
      orderFrom: null,
      orderTill: null,
      locations: [],
      /*Paginations */
      perPage: 10,
      currentPage: 1,
      totalRows: 0,
      sortBy: "requestedOn",
      sortDesc: true,
      AllText: "",
      SubmittedText: "",
      ApprovedText: "",
      ProcessingText: "",
      InTransitText: "",
      RejectedText: "",
      ClosedText: "",
      Alltab: true,
      token: "",
      PPFields: [
        {
          key: "poNum",
          label: "Purchase Order Number"
        },
        {
          key: "productName",
        },
        {
          key: "printOrderNum",
          label: "Print Order Number",
          sortable: true,
        },
        {
          key: "expectedDate",
          sortable: true,
        },
        {
          key: "status",
          sortable: true,
        },
        {
          key: "location",
          sortable: true,
        },
        {
          key: "actionPP",
          label: "Actions",
        },
      ],
      adminFields: [
        {
          key: "poNum",
          label: "Purchase Order Number"
        },
        {
          key: "productName",
        },
        {
          key: "printOrderNum",
          label: "Print Order Number",
          sortable: true,
        },
        {
          key: "expectedDate",
          sortable: true,
        },
        {
          key: "status",
          sortable: true,
        },
        {
          key: "location",
          sortable: true,
        },
        {
          key: "actionAdmin",
          label: "Actions",
        },
      ],
    };
  },
  created() {
  },
  components: {},
  computed: {
    getfields: function () {
      if (this.$store.state.clientAppDecodedToken.role == 'PrintPartner')
        return this.PPFields;
      return this.adminFields;
    }
  },
  methods: {
      createOrder: function() {
        this.$router.push({ name: 'PrintOrderPlacement' });
      },
    onStatusFilterClicked(status){
      if(status=="All")
      {
        this.orderStatus=[];
        this.$root.$emit('bv::refresh::table', 'orderList');
        this.Alltab = true;
        var items=document.getElementsByName('orderlist');
				for(var i=0; i<items.length; i++){
					if(items[i].type=='checkbox')
						items[i].checked=false;
				}
        return;
      }
      var statusInt = parseInt(status)
      const index = this.orderStatus.indexOf(statusInt);
      if (index > -1) {
        this.orderStatus.splice(index, 1);
        this.Alltab = false;
      }
      else{
          this.orderStatus.push(statusInt);
          this.Alltab = false;
      }
    
    //force refresh on status change
    this.$root.$emit('bv::refresh::table', 'orderList');
    },	
    getPrintOrders: function (ctx) {
      const promise = axios({
        url: "/PrintOrder/GetPrintOrders",
        method: "post",
        baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
        data: {
        //   mfIds: this.mfIds,
          searchText: ctx.filter,
          locations: this.locations,
          printOrderStatuses: this.orderStatus,
          ordersFrom: this.orderFrom,
          ordersTill: this.orderTill,
          sortBy: ctx.sortBy,
          sortByDesc: ctx.sortDesc,
          pageNo: ctx.currentPage,
          pageSize: ctx.perPage,
        }
      });

      return promise.then((response) => {
        const items = response.data.miniPrintOrderDtos;
        this.totalRows = response.data.totalCount;
        this.AllText = "All(" + response.data.totalCount + ")";
        this.SubmittedText = "Submitted(" + response.data.submittedCount + ")";
        this.ProcessingText = "Processing(" + response.data.processingCount + ")";
        this.InTransitText = "In Transit(" + response.data.inTransitCount + ")";
        this.RejectedText = "Rejected(" + response.data.rejectedCount + ")";
        this.ClosedText = "Closed(" + response.data.closedCount + ")";
        return items || [];
      });
    },
    closeOrder(printOrderId) {
        axios.post("/PrintOrder/CloseOrder",{
                orderId: printOrderId,
                comments: "Test"
            },
            {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
            }
        ).then(() => {
            // this.$router.push({ name: 'PrintOrders' });
        });
    }
  },
};
</script>
